/* @flow */

import React from 'react';
import styled from 'styled-components';
import patternBg from './Seam-less-Texture-Pack.jpg';

const Container = styled.div`
  background: #ffeac9;
  overflow: hidden;
  min-height: 100vh;
`;

type Props = {};

class Component extends React.Component<Props> {
  render() {
    return (
      <Container>
        {this.props.children}
      </Container>
    );
  }
}

export default Component;



