/* @flow */

import React from 'react';
import styled from 'styled-components';
import Link from '../Link'
import logoLong from '../../logo/Free_Reiki_Course_long_light_text.png'

const Container = styled.div`
  width: 100%;
  height: 60px;
  background: #362c2a;
  overflow: hidden;
`;

const StyledLink = styled(Link)`
  height: 60px;
  line-height: 60px;
  color: #f2ce94;
  padding: 0 10px;
  font-weight: bold;
  border-bottom: 3px solid transparent;
  display: inline-block;
  box-sizing: border-box;
  transition: 0.3s all;
  cursor: pointer;
  
  &:hover {
    border-bottom: 3px solid #e73333;
  }
`;

const StyledLogo = styled.img`
  height: 60px;
  float: left;
`;

type Props = {};

class Component extends React.Component<Props> {
  render() {
    return (
      <Container>
        <Link href="/"><StyledLogo src={logoLong} alt="free reiki course logo"/></Link>
        <StyledLink href="/">Home</StyledLink>
        <StyledLink href="/free_reiki_courses_11.html/">Reiki Level 1</StyledLink>
        <StyledLink href="/advanced_practitioner_level_2_12.html/">Reiki Level 2</StyledLink>
        <StyledLink href="/page_13.html/">Reiki Level 3/Master</StyledLink>
        <StyledLink href="/free_attunements_10.html/">Attunements</StyledLink>
        <StyledLink href="/reik_products_14.html/">Products</StyledLink>
        <StyledLink href="/contact_us_4.html/">About us</StyledLink>
      </Container>
    );
  }
}

export default Component;



