/* @flow */

import React from 'react';
import styled from 'styled-components';
import {Parallax} from 'react-parallax';
import Link from '../Link';
import mainImage from './meditation-2214532_small.jpg';
import {ContentCTA, ContentSecCTA} from '../common';

const Container = styled.div`
  height: 400px;
  width: 100%;
`;

const StyledParallax = styled(Parallax)`
  height: 400px;
  
  & > div {
    height: 100%;
  }
`;

const StyledTitle = styled.h2`
  margin: 15px;
  color: #e72f34;
  background: rgba(54, 44, 42, 0.95);
  padding: 10px;
  display: inline-block;
`;

const MainCTA = styled(Link)`
  display: inline-block;
  background: rgb(252, 154, 29);
  left: 262px;
  position: absolute;
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  border: 4px solid #362c2a;
  color: #362c2a;
  transition: 0.3s all;

  &:hover {
    background: #e72f34;
    left: 270px;
    letter-spacing: 1.2px;
  }
`;

const CTAWrapper = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background: #ffffffb0;
  padding: 20px;
`;


const TitleContainer = styled.div`
  width: 640px;
  margin: 0 auto;
  position: relative;
  height: 100%;
`;


type Props = {};

class Component extends React.Component<Props> {
  render() {
    return (
      <Container>
        <StyledParallax bgImage={mainImage} strength={400}>
          <TitleContainer>
            <br/>
            <StyledTitle>{this.props.title}</StyledTitle>
          </TitleContainer>
          <CTAWrapper>
            <ContentCTA href="/free_reiki_courses_11.html/">Start with Reiki Level 1 for free</ContentCTA>
            <br/>
            <ContentSecCTA href="/reiki-healing/part-1/">Free reiki healing [NEW]</ContentSecCTA>
          </CTAWrapper>
        </StyledParallax>
      </Container>
    );
  }
}

export default Component;



